<template>
  <div>
    <form @submit.prevent="submitHandler" class="" action="/" method="post">
      <div class="flex">
        <div class="input-wrapp">
          <input
            v-model="formData.companyName"
            class="form-control"
            type="text"
            placeholder="Название компании">
          <span class="error" v-if="errors.companyName">Ошибка</span>
        </div>
        <div class="input-wrapp">
          <input
            v-model="formData.email"
            class="form-control"
            placeholder="Email">
          <span class="error" v-if="errors.email">Ошибка</span>
        </div>
        <div class="input-wrapp">
          <the-mask
            novalidate
            v-model="formData.phone"
            class="form-control" placeholder="Телефон"
            :mask="['+38(0##) ###-##-##', '+38(0##) ###-##-##']"
          />
          <span class="error" v-if="errors.phone">Ошибка</span>
        </div>
      </div>
      <div>
        <button data-hover-text="Создать чат-бот" class="b-btn b-btn_dark with-hover-text" type="submit">
          <span class="txt">Попробовать бесплатно</span>
        </button>
      </div>

    </form>
  </div>
</template>

<script>
import {TheMask} from 'vue-the-mask';
import { validation } from "../mixins/validations";
export default {
  name: 'RegisterForm',
  components: {TheMask},
  mixins: [validation],
  data() {
    return {
      formData: {
        companyName: '',
        email: '',
        phone: ''
      },
      errors: {
        companyName: false,
        email: false,
        phone: false
      }
    }
  },
  methods: {
    submitHandler() {
      this.validate(this.formData.email, 'email');
      this.validate(this.formData.phone, 'phone');
      this.validate(this.formData.companyName, 'companyName');
      if (this.isFormValid() === false) {
        alert("Форма не валидна");
        return false;
      } else {
        alert("Отправка форы");
      }

    },
    isFormValid() {
      let errorsValues = Object.values(this.errors);
      let isHasErrors = errorsValues.some(function(item){
        return item == true;
      });
        return !isHasErrors;
    }
  }
}
</script>

